.admin-form-inputs {
  width: 50% !important;
}

.dswp-skip-nav {
  position: absolute; /* Position off-screen */
  left: -9999px; /* Move off-screen */
  background: #fff; /* Background color */
  color: #000; /* Text color */
  padding: 8px;
  z-index: 1000; /* Ensure it is above other content */
  transition: left 0.3s; /* Smooth transition */
}

.dswp-skip-nav:focus {
  left: 0; /* Bring into view when focused */
}

.dswp-skip-nav:active,
.dswp-skip-nav:focus-visible {
  left: 0; /* Keep it in view when active or focused */
}

.dswp-skip-nav-list {
  list-style: none;
  margin: 0; /* Remove default margin */
  padding: 0; /* Remove default padding */
}